



.navbarContainer {
    position: absolute;
    top: 0;
    left: 0;
    border-bottom-right-radius: 0.5rem;
    overflow: hidden;
}

.navbarButton {
    padding: 0.5rem;
    background-color: rgb(192, 227, 37);
    border: none;
    box-shadow: inset 0 0 0.2rem #333;
    cursor: pointer;
}