

.listContainer {
    height: 100%;
    width: 100%;
    border-radius: 0;
    overflow: auto;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    
    @media screen and (min-width: 768px) {
        width: 40%;
        border-radius: 1.5rem;
    }
    
    @media screen and (min-height: 568px) {
        height: 60%;
        border-radius: 1.5rem;
    }
}